.c {
  height: 110vh;
  padding-top: 5vh;
}
.c-wrapper {
  padding: 50px;
  display: flex;
}
.c-left {
  flex: 1;
}
.c-right {
  
  height: 500px;
  width: 60%;
  background-color: #adb0e9;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin: auto;
  padding: 10px;
}

.c-title {
  font-size: 50px;
  width: 80%;
  color:#4070f4;;
  text-align: left;
  font-weight: bold;
}
.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px;
}
.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}
.c-desc {
  font-weight: 200;
}
#contacttt {
  font-Weight:800;
  color:#4070f4;
  text-Decoration:underline;
}
form {
  margin-top: 15px;
  justify-content: center;
  align-items: center;
  /* margin-left: 12%; */
  border: none;
}

form > input::placeholder {
  color: black;
}
form > input {
  width: 80%;
  height: 45px;
  /* border: none; */
  /* border-bottom: 1px solid black; */
  font-size: 17px;
  border-radius: 10px;
  /* margin-bottom: 10px;  */
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}



textarea {
  width: 80%;
  border-bottom: 1px solid black;
  font-size: 15px;
  padding: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

form > button {
  border: none;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
  font-weight: 500;
  width: 160px;
  cursor: pointer;
  border-radius: 10px;
  margin-left: 30%;
  font-size: 19px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
form > button::placeholder {
  color: black;
}
@media all and (max-width: 1000px) {
  .c {
    height: 130vh;
    padding-top: 5vh;
  }
  .c-wrapper {
    padding: 50px;
    display: flex;
    flex-direction: column;
  }
  .c-left{
    margin-left: 20%;
  }
  .c-right {
    height: 500px;
    width: 90%;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin: auto;
    border: 2px solid;
    padding: 10px;
  }
  
}
@media all and (max-width: 610px) {
  .c-left{
    margin-left: 5%;
  }
  .c-left>h1{
    font-size: 30px;
    text-align: center;
  }
  .c-right {
    height: 500px;
    width: 100%;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin: auto;
    border: 2px solid;
    padding: 10px;
  }
  .c-right>h1{
    padding-left: 30px;
  }
}

@media all and (max-width: 399px) and (min-width: 100px){

  .c-left{
    margin-left: -50px;
  }
  .c-right {
    margin-top: 100px;
  }
  #contactBtn{
    margin-left: 20px;
    margin-top: 30px;
  }

  .c-title{
    margin-left: 65px;
    text-align: center;
  }

  #contacttt {
      font-Weight:800;
      color:#4070f4;
      text-Decoration:underline;
      margin-left: -30px;
  }
}